
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
components: {
XHeader,
Search,
Group,
Cell,
XButton, Swipeout, SwipeoutItem, SwipeoutButton
},
data() {
return {
results: [],
value: 'test',
routeName: this.$route.name,
teacherShowList: [
{ id: 1, name: '老师奥顿1', leave: '正常离开', className: '三年级四班', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 2, name: '老师奥顿2', leave: '正常离开', className: '三年级四班', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/xiaoji.jpg' },
{ id: 3, name: '老师奥顿3', leave: '正常离开', className: '三年级四班', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
{ id: 4, name: '老师奥顿4', leave: '正常离开', className: '三年级四班', time: '2017-07-19 18:29:00', 'img': '../../../../static/imgs/my.jpg' },
],
}
},
methods: {
setFocus() {
this.$refs.search.setFocus()
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
this.results = val ? getResult(this.value) : []
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
}
},
created() {
document.title = this.$route.name
}
}

function getResult(val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



